.popup__content-del-card {
        display: flex;
        flex-direction: column;
        background: #FFFFFF;
        border-radius: 10px;
        box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
        width: 430px;
        height: 181px;
    }
    
    @media screen and (max-width: 768px) {
        .popup__content {
            margin: 50% auto;
        }
    }
    
    
    @media screen and (max-width: 470px) {
        .popup__content {
            width: 282px;
        }

}